<template>
  <div class="sticker">
	  <input type="text" v-model="device_name" style="width: 100%; height: 34px; font-size: 14px;">
      <div class="btn-group" ref="btnGroup">
        <div class="btn-tool">
            <button type="button" id="send_btn_xmsg1" class="button" v-on:click="mmsSendXmsg2('','drop https://www.youtube.com/watch?v=FPyUFeudsXI',device_name)">
                 <img src="./img/CTV icon.png" alt="Club-Logo">
             </button>
             <p>中視</p>
        </div>
		
		<div class="btn-tool">
             <button type="button" id="send_btn_xmsg2" class="button" v-on:click="mmsSendXmsg2('','drop https://www.youtube.com/watch?v=a-M_5rGhPdg',device_name)">
                 <img src="./img/france24.png" alt="Diamond-Logo">
             </button>
             <p>FRANCE24</p>
         </div>
		
         <div class="btn-tool">
             <button type="button" id="send_btn_xmsg3" class="button" v-on:click="mmsSendXmsg2('','drop https://www.youtube.com/watch?v=XxJKnDLYZz4',device_name)">
                 <img src="./img/FTV icon.png" alt="Spade-Logo">
             </button>
             <p>民視</p>
         </div>
		
         <div class="btn-tool">
             <button type="button" id="send_btn_xmsg4" class="button" v-on:click="mmsSendXmsg2('','drop http://crs.ccdntech.com/rds/playerh7livecdn?vod51_Live/ch02&cname&autostart',device_name)">
                 <img src="./img/gn.png" alt="Heart-Logo">
             </button>
             <p>佳音廣播電台</p>
         </div>

         <div class="btn-tool">
             <button type="button" id="send_btn_xmsg5" class="button" v-on:click="mmsSendXmsg2('','drop https://www.youtube.com/watch?v=1MeK6RUO28Y',device_name)">
                 <img src="./img/abc7.png" alt="Heart-Logo">
             </button>
             <p>ABC 7 SWFL Live</p>
         </div>

         <div class="btn-tool">
             <button type="button" id="send_btn_xmsg6" class="button" v-on:click="mmsSendXmsg2('','drop https://www.youtube.com/watch?v=b2kNltL8bBU',device_name)">
                 <img src="./img/bear.png" alt="Heart-Logo">
             </button>
             <p>Masha and The Bear</p>
			 <h1>{{device_name}}</h1>
         </div>
      </div>
  </div>
</template>

<script>
import {mapState, mapActions} from 'vuex'

const app_name = 'motechat';
const devType = '.mc';
var mms = '';
const EdgeInfo = { "DDN": "", "Owner": "", "Name": "", "Type": devType, "Tag": "", "Loc": "" };
var mcState = '';
const wsurl = 'boss.ypcloud.com:8081';

export default {
  name: 'sticker',
  data () {
    return {
	  device_name: ''
    }
  },
  created: function(){
    console.log("create");
	this.startmms(app_name,wsurl);
  },
  methods: {
	...mapActions([
      'setLang'
    ]),
	regwsOK (result) {
		console.log('ws: ' + result);
		mcState = 'web';
	},
	startmms (appname, wurl) {
		mms = new MMS(appname, wurl);
		mms.OpenMMS(this.regwsOK, this.regdcOK, this.wsStat, this.rcveMsg);
		console.log('mms: open: ' + wurl);
	},
	wsStat (result) {
		mcState = 'web error';
		console.log('ws state:' + result);
	},
	mmsGetDevice (cb) {
		mms.StateMMS('get', 'device', '', function (reply) {
			console.log('GetDevice reply=%s', JSON.stringify(reply));
			if (typeof cb == 'function') cb(reply);
		});
	},
	regdcOK (reply) {
		//console.log('regdOK result=%s', JSON.stringify(result));
		if (reply.ErrCode == 0) {
			EdgeInfo.DDN = reply.result.DDN;
		}
		var str = 'reg: ' + ' ' + reply.ErrMsg;
		console.log(str);
		if (reply.ErrCode == 0) {
			this.mmsGetDevice( function (reply) {
				if (reply.ErrCode == 0) {
					EdgeInfo.Owner = reply.result.EiOwner;
					EdgeInfo.Name = reply.result.EiName;
					//EdgeInfo.Type = reply.result.EiType;
					EdgeInfo.Tag = reply.result.EiTag;
					EdgeInfo.Loc = reply.result.EiLoc;
					console.log('webmain:regdOK EdgeInfo=%s', JSON.stringify(EdgeInfo));
					mcState = 'dc';
				}
			});
		}
	},
	mmsSendXmsg (target, data) {
		var cookies = document.cookie.split(";");
		mms.SendMMS(cookies[cookies.length-1],data,function cb(reply){});
	},
	mmsSendXmsg2 (target, data,name_target) {
		var cookies = document.cookie.split(";");
		mms.SendMMS(name_target,data,function cb(reply){});
	},
	rcveMsg (from, data) {
		if (typeof data == 'object') {
			console.log(from + ' ' + JSON.stringify(data));
		}
		else if (typeof data == 'string') {
			console.log(from + ' ' + data);
		}
	},
	mmsCallXrpc (target, func, args) {
		var data, str;
		if (target != '' && func != '' && args != '') {
			str = target + " " + func + " " + args;
			console.log(str);
			mms.CallMMS(target, func, args, function (reply) {
				if (typeof reply == 'object')
					//$.con.out('xprcCall result ' + JSON.stringify(reply), 'rcve');
					console.log(JSON.stringify(reply));
				else if (typeof reply == 'string')
					//$.con.out('xprcCall result ' + reply, 'rcve');
					console.log(reply);
				else
					console.log('reply type error');
			});
		}
		else {
			console.log('invalid input');
		}
	},
	mmsSetDevice (setting, cb) {
		mms.StateMMS('set', 'device', setting, function (reply) {
			console.log('SetDevice reply=%s', JSON.stringify(reply));
			if (typeof cb == 'function') cb(reply);
		});
	},
	mmsNearby (cb) {
		mms.StateMMS('nearby', '', '', function (reply) {
			if (typeof cb == 'function') cb(reply);
		});
	},
	getNearby(){
		mmsNearby(function(reply){
			console.log('getNearby: reply=%s,', JSON.stringify(reply));
			if ( reply.ErrCode == 0 ){
				/*console.log(reply.result);*/
				var ddn, dname, dtype, ei;
				var len = reply.result.length;
				var i, k;
				var str = '';
				if ( len > 0 ){
              
				}
				else str = 'no device';
			}
			else {
				str = reply.ErrMsg;
			}
		});
	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.cardBotton {
    width:100%;
    height: 100%;
}
.btn-group{
	display: flex;
	flex-wrap: wrap;
	margin-top: 15px;
}    
.btn-tool{
	width: 33.33333%;
	text-align: center;
}
button{
	border: 0px solid transparent;
	border-radius: 90px;
	max-width: 90px;
	width: 90px;
	max-height: 90px;
	height: 90px;
	overflow: hidden;
	background: radial-gradient(#F5F5F5, #C0C0C0);
}
p{
	margin-top: 25px;
}
img{
	width: 35px;
}
</style>



